@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: "Poppins", sans-serif;
}

html{background:#dedede}#review-page{display:grid;grid-template-columns:5fr 2fr;grid-template-areas:"main aside";grid-gap:30px;gap:30px;margin:3rem 10rem}.content-wrapper-frontpage{background:url(/reviews/static/media/bg.f6b0543d.jpg) no-repeat;background-position:center bottom;background-size:cover;height:calc(100vh - 250px);display:flex;justify-content:center;align-items:center}.content-wrapper{margin:3rem 10rem}main#review{grid-area:main;display:grid;grid-auto-rows:minmax(-webkit-min-content, -webkit-max-content);grid-auto-rows:minmax(min-content, max-content);grid-template-areas:"Content"  "Comments";grid-gap:30px;gap:30px}main#frontpage{text-align:center;margin-top:-10rem}aside{grid-area:aside}.container{width:100%;padding:1rem;background:#fff;border-radius:5px}.header-container{display:flex;justify-content:space-between;align-items:center;background:#fff;padding:1rem 10rem}.header-container .search input[type="search"]{width:400px;height:60px;padding:12px 20px}.header-container .search input[type="search"]:focus{outline:none}.header-container .search input[type="search"]:active{outline:none}.header-container .logo{background:url(data:image/png;base64,) no-repeat;height:67px;width:250px}footer{height:150px;display:grid;grid-template-columns:2fr 1fr;grid-template-areas:"company social-media";background:#fff;padding:1rem 10rem}footer .company{grid-area:company;align-self:center;display:grid;grid-template-columns:80px 1fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"logo company-info"  "logo copyright"  "logo footer-menu"}footer .company .logo{grid-area:logo;align-self:center;background:url(/reviews/static/media/footer-logo.ee2c72c0.svg) no-repeat;height:60px;width:60px}footer .company .company-info{grid-area:company-info;font-weight:700}footer .company .company-info span{font-weight:400;margin-left:0.5rem}footer .company .company-info span::before{content:"\00b7";margin-right:0.5rem}footer .company .copyright{grid-area:copyright}footer .company .footer-menu{display:flex;grid-area:footer-menu;list-style:none}footer .company .footer-menu li{font-weight:400;margin-right:1rem;cursor:pointer;text-decoration:underline}footer .social-media{grid-area:social-media;align-self:center;justify-self:right}footer .social-media span{border:1px solid #d62725;border-radius:50%;padding:15px;margin:10px;cursor:pointer}nav{display:flex !important;background:#fff}nav ul{list-style:none;display:flex;flex-wrap:wrap;justify-content:space-between;justify-self:flex-end;align-items:center}nav ul li{list-style:none;padding:10px 20px}nav ul li a{text-decoration:none}nav ul li a.current-menu-item{color:#d62725}#analytics{display:grid;grid-template-columns:1fr 1fr;grid-template-areas:"Logo Analytics";border-radius:5px;padding:2rem}#analytics .logo{grid-area:Logo;align-self:center;justify-self:center}#analytics .logo img{height:100px;width:auto;object-fit:cover;object-position:center center}#analytics .analytics{display:grid;grid-template-columns:30% 70%;grid-template-rows:1fr 1fr 1fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"Gauge Name"  "Gauge total-respondents"  "Gauge spread";grid-area:Analytics}#analytics .analytics .gauge{grid-area:Gauge;align-self:center;justify-self:center}#analytics .analytics .name{grid-area:Name;align-self:center;font-size:28px;font-weight:700}#analytics .analytics .total-respondents{grid-area:total-respondents;align-self:center}#analytics .analytics .spread{display:grid;grid-template-columns:15% 30% 55%;grid-template-areas:"detractors passive promoters";grid-area:spread;height:30px;text-align:center;align-items:center}#analytics .analytics .spread .detractors{grid-area:detractors;background:red;color:white;padding:5px 0px}#analytics .analytics .spread .passive{grid-area:passive;background:yellow;padding:5px 0px}#analytics .analytics .spread .promoters{grid-area:promoters;background:green;color:white;padding:5px 0px}#responsesContainer{grid-area:Comments;display:grid;grid-gap:10px;gap:10px}#responsesContainer .container{padding:2rem;border-radius:5px;min-height:150px}#responsesContainer .response{display:grid;grid-template-columns:80px auto;grid-template-areas:"rating user"  "rating comment"}#responsesContainer .rating{grid-area:rating;justify-self:center}#responsesContainer .rating .score{background:#00a46e;width:50px;height:50px;border-radius:50%;display:flex;justify-content:center;align-items:center;color:#fff}#responsesContainer .user{grid-area:user;font-weight:700;font-size:20px}#responsesContainer .user span{display:block;font-weight:400;font-size:14px}#responsesContainer .comment{grid-area:comment}#sidebars{grid-area:Sidebar;display:grid;grid-gap:30px;gap:30px}#sidebars .container{border-radius:5px}#sidebars p{margin:10px 0}#frontpage input[type="search"]{width:800px;height:80px;padding:12px 20px;font-size:22px;border:none;border-radius:10px}#frontpage input[type="search"]:focus{outline:none}#frontpage input[type="search"]:active{outline:none}#frontpage input[type="search"]:not(:-ms-input-placeholder){border-bottom-left-radius:0px;border-bottom-right-radius:0px}#frontpage input[type="search"]:not(:placeholder-shown){border-bottom-left-radius:0px;border-bottom-right-radius:0px}#frontpage .search-result-wrapper .search-results{width:800px;list-style-type:none;text-align:left;background:#fff;position:absolute}#frontpage .search-result-wrapper .search-results a{text-decoration:none;color:#3c3c3c}#frontpage .search-result-wrapper .search-results li{padding:15px 12px;transition:0.1s all ease-in-out}#frontpage .search-result-wrapper .search-results li:hover{transition:0.1s all ease-in-out;background-color:#f2f2f2;color:#d62725}.btn{border:none;color:#fff;height:60px;width:120px;cursor:pointer}.btn:focus{outline:none}.btn:active{outline:none}.btn.btn-primary{background-color:#d62725}.btn.btn-primary:hover{background-color:#dd3a38}.btn.btn-link{background-color:transparent;color:#d62725}.btn.btn-link:hover{color:#dd3a38;text-decoration:underline}

